function InputCleaner(input, maxlength = 25) {
  const trimmed = input.trim();
  const escaped = trimmed.replace(
    /[&<>"']/g,
    (match) =>
      ({
        "&": "&amp;",
        "<": "&lt;",
        ">": "&gt;",
        '"': "&quot;",
        "'": "&#39;",
      }[match])
  );
  const limited = escaped.slice(0, maxlength);
  return { trimmed, escaped, limited };
}

export default InputCleaner;
